
import wx from 'weixin-js-sdk'
import { mapGetters,mapActions }from 'vuex'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
components: {
Swipeout, SwipeoutItem, SwipeoutButton,LoadMore
},
data () {
return {
pageSize:10,
sy:0,
ey:0,
onFetching:false,
tips:'',
listObj:{
max:'',
list:[]
}
}
},
created(){
document.title = "浏览记录"
if(this.$route.query.token){
localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
}
if(this.$route.query.refreshToken){
localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
}
this.getData(0)
},
methods: {
...mapActions(['browseHistorys','delBrowseHistorys']),
ontouchstart(e){
this.sy = e.changedTouches[0].pageY
},
ontouchend(e){
this.ey = e.changedTouches[0].pageY
if(this.sy - this.ey > 30){
if(!this.onFetching){
this.bindScroll()
}
}
},
bindScroll(){

let no = this.listObj.list.length
let to = this.listObj.max
if(no >= to){
this.tips='没有更多了'
this.onFetching = true
return
}
let app = this.$refs.outer
let dh = app.scrollTop
let h = app.clientHeight
let bh = this.$refs.comd.$el.clientHeight
console.log(dh)
console.log(bh)
console.log(h)
if(bh - dh <= h ){

this.onFetching = true
this.tips='正在加载'
this.getData(parseInt(no/this.pageSize),'',1)

}
},
goHome(){
wx.miniProgram.switchTab({
url:'/pages/home/index'
})
},
goTo(id){
wx.miniProgram.navigateTo({
url:'/pages/goodDetail/index?id='+ id
})
},
delJv1(){
this.delBrowseHistorys({data:{isAllClear:true},this:this})
},
delJv(id){
this.delBrowseHistorys({data:{browseHistoryId:id},this:this})
},
getCopy(ary){
if(ary.add){
this.listObj.list.push(...ary.data.recordsList)
}
else{
this.$set(this.listObj,'max',ary.data.totalSize)
this.$set(this.listObj,'list',ary.data.recordsList)
}
},
getData(i,j,m){
this.showLoading()
this.browseHistorys({this:this,data:{viewIndex:i,viewSize:this.pageSize},method:this.browseHistorys,fn:j,add:m})
},
handleEvents (type) {
console.log('event: ', type)
}
}
}



